.main-header {
  position: sticky;
  top: 0;
  z-index: 1;
  margin: 0;
  min-height: 120px;
  padding: 30px 20px 35px;
  background: linear-gradient(180deg, $text-color 84.9%, rgba(4, 24, 39, 0) 100%);
}

.main-header__wrapper {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  width: 89.7%;
  max-width: $vp-1024;

  @media (max-width: $desktop-width) {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    width: 89.7%;
    max-width: $vp-1024;
  }
}

.main-header__set-call {
  align-items: center;
  margin: -8px 60px 0 0;
  border-radius: 3px;
  border: 1px solid $color-alice-blue-lightest;
  background: linear-gradient(180deg, $text-color 84.9%, rgba(4, 24, 39, 0) 100%);

  @media (max-width: ($vp-768 - 1px)) {
    display: none;
  }

  @include vp-1023 {
    margin-left: clamp(5px, 6.7%, 60px);
    display: none;
  }
}

